import React, { useState } from "react";
import { useGlobalContext } from "../context";
import cartLogo from "../images/cart.png";
import { FaTimes } from "react-icons/fa";
import { links, social } from "../data";
import CustomerInfo from "./CustomerInfo";
import PhoneBox from "./phone-box";

const Cart = () => {
  const {
    isCartOpen,
    closeCart,
    cart,
    deleteFromCart,
    emptyCart,
    cartTotal,
    cartCount,
    incProductQuantityInCart,
    decreasProductAmount,
    updateProductQuantity,
    showCustomerInfo,
    isCustomerInfoShown,
  } = useGlobalContext();
  const [isPhoneBoxShown, setIsPhoneBoxShown] = useState(false);
  const showPhoneBox = () => {
    setIsPhoneBoxShown(true);
  };
  const doNothing = () => {};
  return (
    <div className="cart-scroll">
      <aside className={isCartOpen ? "cart show-cart" : "cart "}>
        <div className="cart-header">
          <button className="cart-close" onClick={closeCart}>
            <FaTimes />
          </button>
          <h3> </h3>
          <i className="fa fa-cart-shopping cart-logo">
            <span>{cartCount > 0 ? cartCount : ""}</span>
          </i>
        </div>
        <div className="cart-body">
          <table className="table">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th className="amount-caption"></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {cart.map((product) => {
                let sumOfRow = product.unitPrice * product.amount;
                sumOfRow = parseFloat(sumOfRow.toFixed(2));

                return (
                  <tr key={product.id}>
                    <td className="counter">
                      <span id="counter"></span>
                    </td>
                    <td>{product.name}</td>
                    <td>
                      {product.unitPrice}
                      <span> </span>
                    </td>
                    <td>
                      <div className="amount under-btn ">
                        <button
                          onClick={() => incProductQuantityInCart(product)}
                        >
                          +
                        </button>
                        <input
                          type="text"
                          value={product.amount + " " + product.packType}
                          onChange={updateProductQuantity}
                        />

                        <button onClick={() => decreasProductAmount(product)}>
                          -
                        </button>
                      </div>
                      {/* <input
                      className="input-amount"
                      type="number"
                      value={product.amount}
                      min="1"
                      onChange={doNothing}
                    />
                    <span> {product.unitName}</span> */}
                    </td>
                    <td>
                      {sumOfRow} <i className="fa-solid fa-shekel-sign"></i>
                    </td>
                    <td>
                      <button
                        className="remov-btn"
                        onClick={() => deleteFromCart(product.id)}
                      >
                        
                      </button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
          <div className="cart-summary">
            <span></span>
            <input
              id="total"
              type="text"
              value={cartTotal}
              onChange={updateProductQuantity}
            />
            <span> </span>
            <div>
              <button id="empty-cart" onClick={emptyCart}>
                  <i className="fas fa-trash"></i>
              </button>
              <br></br>
              <button id="empty-cart" onClick={showPhoneBox}>
                <a href="#phone-box"> </a>
              </button>
            </div>
          </div>
        </div>

        <br></br>
        {isPhoneBoxShown && <PhoneBox />}
        {isCustomerInfoShown && <CustomerInfo />}
      </aside>
    </div>
  );
};
export default Cart;
